.side-menu {
  /* border-right: 1px solid rgb(141, 141, 141); */
  height: 100%;
  /* overflow-y: scroll; */
  /* background: #141824; */
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  overflow-x: hidden;
}
/* For webkit-based browsers (Chrome, Safari) */
.side-menu::-webkit-scrollbar {
  width: 4px; /* Set the width of the scrollbar */
}

/* Handle */
.side-menu::-webkit-scrollbar-thumb {
  background: rgba(141, 141, 141, 0.7); /* Color of the scrollbar handle */
  border-radius: 10px; /* Round the scrollbar handle */
}

/* Handle on hover */
.side-menu::-webkit-scrollbar-thumb:hover {
  background: rgba(141, 141, 141, 1); /* Color on hover */
}
.route-style {
  display: flex;
  color: white;
  justify-content: space-around;
  align-items: center;
}
.route-style {
  margin-left: 30px;
  color: #b1bacf;
}
.route-style span {
  margin-right: 10px;
}
.admin-routes {
  text-decoration: none;
  list-style: none;
  display: flex;
  /* justify-content: center; */
  align-items: center;
  width: 100%;
  padding: 14px 0px;

  border: 1px solid transparent;
}
.admin-routes:hover {
  background: rgb(141, 141, 141);
  border: 1px solid rgb(141, 141, 141);
  border-radius: 10px;
}
.admin-routes:hover .route-style {
  color: white;
}
.rotate {
  transform: rotate(180deg);
}
.c-hover {
  text-decoration: none;
  list-style: none;
  display: flex;
  /* justify-content: center; */
  align-items: center;
  width: 100%;

  cursor: pointer;
  padding: 15px 0px;
}
.c-hover:hover .route-style {
  color: white;
}
